<template>
		<div class="container-fluid  p-0 main">
		<cust-header variant="Кашигин С.С." group="P3210" name="1055"></cust-header>
		<div class="row my-row">
			<div class="col-sm-12 order-md-last col-md-6 my-col">
				<Graph v-bind:r="r" v-bind:x="x" v-bind:y="y" v-bind:trigger="trigger" v-on:clear="deleteAllPoints()" v-bind:points='points' v-on:add-point="addPoint"></Graph>
			</div>
			<div class="col col-md-6 my-col">
				<ControllPanel v-bind:r='r' v-on:change-r="changeR" v-on:change-x="changeX" v-on:change-y="changeY" v-on:add-point="addPoint"></ControllPanel>
			</div>
		</div>
		<div class="row justify-content-center my-row">
			<div class="col-6 my-col">
				<a href="/TaskListManager/login.html">
					<button type="button">Log out</button>
				</a>
			</div>
			<div class="col-6 my-col">
				<point-list v-on:point-deleted="deletePoint" v-bind:points="points"></point-list>
			</div>
		</div>
		<!--
		<div class="row my-row">
			<button v-on:click="showPoints">HEH</button>
		</div>
		-->
	</div>	
</template>

<script>
import CustHeader from './Header.vue'
import ControllPanel from './ControllPanel.vue'
import Graph from './Graph.vue'
import PointList from './PointList.vue'
import $ from 'jquery';

	export default{
		data() {
			return {
				trigger : false,
				points:[],
				r: 2,
				x: 0,
				y: 0,
			};
		},
		components: {
			CustHeader,
			ControllPanel,
			Graph,
			PointList,
		},
		methods: {
			deleteAllPoints() {
				$.ajax({
					type: 'head',
					//url: 'http://localhost:9000/TaskListManager/rest/point/clear',
					url: '/TaskListManager/rest/point/clear',
					success:() => {
						this.points.splice(0,this.points.length);
						this.callTrigger()
					}
				});
				// this.points.splice(0,this.points.length);
			},
			deletePoint(id){
				$.post(
					//'http://localhost:9000/TaskListManager/rest/point/deletepoint',
					'/TaskListManager/rest/point/deletepoint',
					{pointid: id,},
					() => {
						this.loadPoints();
					}
				);
			},
			callTrigger(){
				this.trigger = !this.trigger;
			},
			async addPoint(x, y, r){
				$.post(
					//'http://localhost:9000/TaskListManager/rest/point/check',
					'/TaskListManager/rest/point/check',
					{
						X: x,
						Y: y,
						R: r,
					},
					(data) => {
						/* eslint-disable */
						console.log(data);
						this.points.push(data);
						this.callTrigger();
					},
				);
			},
			showPoints(){
				/* eslint-disable */
				console.log(this.points);
				/* eslint-disable */
				console.log(this.r);
			},
			changeR(newR){
				this.r = newR;
			},
			changeX(newX){
				this.x = newX;
			},
			changeY(newY){
				this.y = newY;
			},
			async loadPoints(){
				$.ajax({
					type: 'get',
					//url: 'http://localhost:9000/TaskListManager/rest/point/getpoints',
					url: '/TaskListManager/rest/point/getpoints',
					success:(data) => {
						/* eslint-disable */
						console.log(data);
						this.points=data;
						this.callTrigger()
					}
				})
			},
		},
		mounted() {
				this.loadPoints();
		},
	};
</script>

<style lang="scss">

@import "../scss/custom.scss";

/*
	.my-col {
		border: 3px dotted blue;
 }	
 .my-row {
		border: 3px solid red;
 }
*/
 	.point-list {
		overflow-y: scroll;
		border: 1px solid;
 	}

	.row :not(.header) {
		margin-top: 5px;
		margin-bottom: 5px;
	}

	button, input {
		margin: 2px;
		margin-bottom: 2px;
		min-width: 50px;
	}
	.header {
		background-color : orange;
		font-size : 18pt;
	}
	@media (max-width: 800px) {
		html {
			width: all;
		}
		body {
			position: fixed;
			width: 100%
		}
		button {
			margin: 0px;
			margin-bottom: 0px;
		}
		.container-fluid{
		  width: 100%;
		  vertical-align: top;
		}
		.main {
			width: 100%;
		}
		.header {
			width: 100%;
		}
		.container-fluid{
	  	max-width: 100%;
		}
		.row :not(.header) {
			margin-top: 1px;
			margin-bottom: 1px;
		}
	}
	@media (min-width: 800px) {
		.container-fluid{
		  max-width: 800px;
		  padding: 0px;
		}
		.main{
			min-width : 800px;
			min-height: 300px;
			display : inline-block;  
			background-color : white;
		}
		body {
			//background-image: url("https://steamuserimages-a.akamaihd.net/ugc/1099167284423480181/B67BFC700AC7CC9DFDDBF179852D84FCDBB5745D/");
			background-image: url("/TaskListManager/resources/img/back.png");
		}
		.main {
			border: 4px double black;
			border-top-right-radius : 14px;
			border-top-left-radius : 14px;
		}
		.header {
			border-top-right-radius : 11px;
			border-top-left-radius : 11px;
		}
 }

</style>