<template>
		<tr>
			<td>
				{{point.id}}
			</td>
			<td>
				{{point.x}}
			</td>
			<td>
				{{point.y}}
			</td>
			<td>
				{{point.r}}
			</td>
			<td>
				{{point.checked}}
			</td>
			<td>
				<button v-on:click="deletePoint(point)">X</button>
			</td>
		</tr>
</template>

<script>
	export default{
		props: ['point'],

		methods: {
			deletePoint(point) {
				this.$emit('delete-point', point);
			},
		},
	};
</script>