<template>
	<div>
		<form onkeydown="return event.key != 'Enter';" method="post">
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="-2" v-on:click="changeX(-2)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="-1.5" v-on:click="changeX(-1.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="-1" v-on:click="changeX(-1)"/>
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				X:
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="-0.5" v-on:click="changeX(-0.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="0" v-on:click="changeX(0)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="0.5" v-on:click="changeX(0.5)"/>
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="1" v-on:click="changeX(1)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="1.5" v-on:click="changeX(1.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="X" value="2" v-on:click="changeX(2)"/>
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col justify-content-start">
				y: 
				</div>
				<div class="col my-col">
				<input type="text" name="Y" size=5 v-model.number="localY" v-on:change="checkY(localY)" placeholder="(-5 ... 3)" />
				</div>
				<div class="col my-col">
				</div>
				<div class="col my-col">
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="-2" v-on:click="changeR(-2)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="-1.5" v-on:click="changeR(-1.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="-1" v-on:click="changeR(-1)"/>
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				R:
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="-0.5" v-on:click="changeR(-0.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="0" v-on:click="changeR(0)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="0.5" v-on:click="changeR(0.5)"/>
				</div>
			</div>
			<div class="row my-row justify-content-center">
				<div class="col my-col">
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="1" v-on:click="changeR(1)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="1.5" v-on:click="changeR(1.5)"/>
				</div>
				<div class="col my-col">
				<input type="button" name="R" value="2" v-on:click="changeR(2)"/>
				</div>
			</div>
			<div class="row my-row ">
				<div class="col my-col justify-content-center">
				x={{localX}} y={{localY}} r={{localR}}
				</div>
			</div>
			<div class="row my-row">
				<div class="col my-col justify-content-center">
				<input type="button" value="Send" v-on:click="addPoint(localX, localY, localR)"/>
				</div>
			</div>
		</form>
	</div>
</template>

<script>
	export default{
		props: ['r'],
		data() {
			return {
				localR : this.r,
				localX : 0,
				localY : 0,
			};
		},
		methods:{
			changeR(r){
				if (r>=0) {
					this.$emit("change-r", r);
					this.localR = r;
				}
				else
					alert("r less then 0");
			},
			changeX(x) {
				this.localX = x;
				this.$emit("change-x", x);
			},
			checkY(y) {
				if (y>-5 && y<3) {
					this.localY = y;
					this.$emit("change-y", y);
				}
				else {
					alert("Y must be in range -5 to 3 excluded");
					this.localY = 0;
				}
			},
			addPoint(x, y, r) {
				this.$emit("add-point", x, y, r);
			},
		},
	}
</script>

<style>

</style>