<template>
	<div>
		<div class="row justify-content-center">
			<div class="col">
				<canvas id="canvas" width="300" height="300" v-on:click="addPointOnClick"></canvas>
			</div>
		</div>
		<div class="col">
			<div class="row justify-content-center">
			<button v-on:click="clearCanvas">Clear Canvas</button>
		</div>
		</div>
	</div>
</template>

<script>
	export default{
		props:['points','trigger', 'r', 'x', 'y'],
		data() {
			return {
				localR : this.r,
				localX :this.x,
				localY :this.y,
			};
		},
		watch: {
			trigger: function() {
				this.drawCanvas();
				this.loadPoints();
				// console.log(this.points);
				this.drawTarger(this.localX, this.localY);
				this.$emit("graph-redrawed");
			},
			r: function() {
				this.localR = this.r;
				this.drawCanvas();
				this.loadPoints();
				this.drawTarger(this.localX, this.localY);
			},
			x: function() {
				this.localX = this.x;
				this.drawCanvas();
				this.loadPoints();
				this.drawTarger(this.localX, this.localY);
			},
			y: function() {
				this.localY = this.y;
				this.drawCanvas();
				this.loadPoints();
				this.drawTarger(this.localX, this.localY);
			},
		},
		methods: {
			drawCanvas() {
				var r = this.localR;
				var canvas = document.getElementById("canvas");
				var context = canvas.getContext("2d");
				context.clearRect(0,0, canvas.width, canvas.height);
				var len = 130/5*r;

				//прямоугольник
				context.beginPath();
				context.rect(150, 150, len/2, -len);
				context.closePath();
				context.strokeStyle = "blue";
				context.fillStyle = "blue";
				context.fill();
				context.stroke();

				// сектор
				context.beginPath();
				context.moveTo(150, 150);
				context.arc(150, 150, len, Math.PI*0.5, Math.PI*2, true);
				context.closePath();
				context.strokeStyle = "blue";
				context.fillStyle = "blue";
				context.fill();
				context.stroke();

				//треугольник
				context.beginPath();
				context.moveTo(150, 150);
				context.lineTo(150-len, 150);
				context.lineTo(150, 150+len/2);
				context.lineTo(150, 150);
				context.closePath();
				context.strokeStyle = "blue";
				context.fillStyle = "blue";
				context.fill();
				context.stroke();

				//отрисовка осей
				context.beginPath();
				context.font = "10px Verdana";
				context.moveTo(150, 0); context.lineTo(150, 300);
				context.moveTo(150, 0); context.lineTo(145, 15);
				context.moveTo(150, 0); context.lineTo(155, 15);
				context.fillText("Y", 160, 10);
				context.moveTo(0, 150); context.lineTo(300, 150);
				context.moveTo(300, 150); context.lineTo(285, 145);
				context.moveTo(300, 150); context.lineTo(285, 155);
				context.fillText("X", 290, 135);

				// деления X
				context.moveTo(145, 20); context.lineTo(155, 20); context.fillText(5, 160, 20);
				context.moveTo(145, 85); context.lineTo(155, 85); context.fillText((5 / 2), 160, 78);
				context.moveTo(145, 215); context.lineTo(155, 215); context.fillText(-(5 / 2), 160, 215);
				context.moveTo(145, 280); context.lineTo(155, 280); context.fillText(-5, 160, 280);
				// деления Y
				context.moveTo(20, 145); context.lineTo(20, 155); context.fillText(-5, 20, 170);
				context.moveTo(85, 145); context.lineTo(85, 155); context.fillText(-(5 / 2), 70, 170);
				context.moveTo(215, 145); context.lineTo(215, 155); context.fillText((5 / 2), 215, 170);
				context.moveTo(280, 145); context.lineTo(280, 155); context.fillText(5, 280, 170);

				context.closePath();
				context.strokeStyle = "black";
				context.fillStyle = "black";
				context.stroke();
				/* eslint-disable */
				console.log("redrawing canwas done!");
			},
			clearCanvas(){
				this.drawCanvas();
				this.$emit("clear");
			},
			drawTarger(xx, yy){
				var x=150 + xx *130 / 5;
				var y=150 - yy * 130 / 5;
				/* eslint-disable */
				console.log(x, y);
				var canvas = document.getElementById("canvas");
				var context = canvas.getContext("2d");
				/* eslint-disable */
				console.log("begin drawing target...");
				context.beginPath();
				context.moveTo(x-10, y);
				context.lineTo(x+10,y);
				context.moveTo(x, y-10);
				context.lineTo(x,y+10);
				context.closePath();
				context.strokeStyle = "grey";
				context.stroke();
			},
			isArea(x, y, r){
				//circle
				if(x >= 0 && y <= 0 && x * x + y * y <= r * r){
					return true;
				}
				//tri
				if(x <= 0 && y <= 0 && y/(-r/2) <= (x+r)/r){
					return true;
				}
				//rect
				if(x >= 0 && y >= 0 && x <= (r/2) && y <= r){
					return true;
				}
				return false;
			},
			drawPoint(x, y, isArea){
				
				var canvas = document.getElementById("canvas");
				var context = canvas.getContext("2d");
				context.beginPath();
				context.rect(x - 2, y - 2, 4, 4);
				context.closePath();
				if(isArea === true){
					context.strokeStyle = "orange";
					context.fillStyle = "orange";
				} else {
					context.strokeStyle = "red";
					context.fillStyle = "red";
				}
				context.fill();
				context.stroke();
			},
			addPointOnClick(){
				var canvas = document.getElementById("canvas");
				var r = this.localR;
				// console.log(r);
				if (r>=0) {
					var br = canvas.getBoundingClientRect();
					var left = br.left;
					var top = br.top;
					var event = window.event;
					var x = event.clientX-left;
					var y = event.clientY-top;
					this.drawPoint(x, y, this.isArea((x - 150) / 130 * 5, (150 - y) / 130 * 5, r));
					this.sendPoint(Math.floor(((x - 150) / 130 * 5)*100)/100, Math.floor(((150 - y) / 130 * 5)*100)/100, this.r);
				}
				else {
					alert("Undefined R.");
				}
			},
			sendPoint(x, y, r) {
				this.$emit("add-point", x, y, r);
			},
			loadPoints(){
				this.points.forEach(point => {
					// console.log(point.id);
					this.drawPoint(150+point.x*130/5, 150-point.y*130/5, this.isArea(point.x, point.y, this.localR));

				})
				/* eslint-disable */
				console.log("all points drawed!");
			},
		},
		mounted() {
			this.drawCanvas();
			this.loadPoints();
		},
	};
</script>

<style>
	canvas {
		outline: black 1px solid;
	}	
</style>