<template>
	<div class="point-list">
		<table>
			<tr>
				<td>id</td>
				<td>x</td>
				<td>y</td>
				<td>R</td>
				<td>checked</td>
				<td></td>
			</tr>
			<tr is="point-ent" v-for="point in points.slice().reverse()" v-bind:key="point.id" v-bind:point="point" v-on:delete-point="deletePoint(point)">
			</tr>
		</table>
	</div>
</template>

<script>
	import PointEnt from './PointEnt.vue'

	export default{
		props: ['points'],
		methods: {
			deletePoint(point) {
				const pointIndex = this.points[this.points.indexOf(point)].id;
				//console.log(pointIndex);
				//this.points.splice(this.points.indexOf(point), 1);
				this.$emit("point-deleted", pointIndex);
			}
		},
		components: {
			PointEnt,
		},
	};
</script>